import React, { useState, useRef } from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import { postLogin, pacthPwfind, postJoin } from "../../apis/axios.js";

const Header = () => {
  const [modalOpen, setModalOpen] = useState(false);
  const [joinModalOpen, setJoinModalOpen] = useState(false);
  const [passwordModalOpen, setPasswordModalOpen] = useState(false);
  const modalBackground = useRef();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [userName, setName] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await postLogin(email, password);
      alert(" ");
      //        .
    } catch (error) {
      setError(error.response ? error.response.data.message : " ");
    }
  };

  const handleSubmit2 = async (e) => {
    e.preventDefault();
    try {
      await pacthPwfind(email);
      alert("   ");
      setPasswordModalOpen(false);
    } catch (error) {
      setError(
        error.response ? error.response.data.message : " ."
      );
    }
  };

  const handleSubmit3 = async (e) => {
    e.preventDefault();
    try {
      await postJoin(userName, email, password);
      alert(" .");
      setJoinModalOpen(false);
    } catch (error) {
      setError(
        error.response ? error.response.data.message : " ."
      );
    }
  };

  return (
    <>
      <div className="Header">
        <div className="Header-left">
          <Link to="/">
            <div className="icon">  </div>
          </Link>
          <div className="head-text">
            <Link to="/gameList"> </Link>
            <Link to="/reviewBoard"> </Link>
          </div>
        </div>
        <div className="Header-right">
          <div onClick={() => setModalOpen(true)}></div>
        </div>
      </div>
      {modalOpen && (
        <div
          className="modal-container"
          ref={modalBackground}
          onClick={(e) => {
            if (e.target === modalBackground.current) {
              setModalOpen(false);
            }
          }}
        >
          <div className="modal-content">
            <div className="icon"></div>
            <form className="login-form" onSubmit={handleSubmit}>
              <div className="login-text">
                <p></p>
                <input
                  type="email"
                  id="email"
                  value={email}
                  placeholder=" ."
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="login-text">
                <p></p>
                <input
                  type="password"
                  value={password}
                  placeholder=" ."
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <div className="modal-button">
                <button type="submit"></button>
              </div>
              {error && <div className="error">{error}</div>}
              <div className="modal-menu">
                <div
                  onClick={() => {
                    setJoinModalOpen(true);
                    setModalOpen(false);
                  }}
                >
                  
                </div>
                <div
                  onClick={() => {
                    setPasswordModalOpen(true);
                    setModalOpen(false);
                  }}
                >
                   
                </div>
              </div>
            </form>
          </div>
        </div>
      )}
      {joinModalOpen && (
        <div
          className="modal-container"
          ref={modalBackground}
          onClick={(e) => {
            if (e.target === modalBackground.current) {
              setJoinModalOpen(false);
            }
          }}
        >
          <div className="modal-content">
            <div className="join"></div>

            <form className="join-form" onSubmit={handleSubmit3}>
              <div className="join-text">
                <p></p>
                <input
                  type="text"
                  placeholder="ex)"
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div className="join-text">
                <p></p>
                <input
                  type="email"
                  placeholder=" ."
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="join-text">
                <p></p>
                <input
                  type="password"
                  placeholder=" ."
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <div className="join-text">
                <p> </p>
                <input
                  type="password"
                  placeholder="   ."
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <div className="modal-joinbutton">
                <button></button>
              </div>
              <div
                className="join-backButton"
                onClick={() => {
                  setJoinModalOpen(false);
                  setModalOpen(true);
                }}
              >
                <button></button>
              </div>
            </form>
          </div>
        </div>
      )}
      {passwordModalOpen && (
        <div
          className="modal-container"
          ref={modalBackground}
          onClick={(e) => {
            if (e.target === modalBackground.current) {
              setPasswordModalOpen(false);
            }
          }}
        >
          <div className="modal-content">
            <div className="password"> </div>
            <form className="login-form" onSubmit={handleSubmit2}>
              <div className="pw-text">
                <p></p>
                <input
                  name="email"
                  type="text"
                  placeholder=" ."
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="modal-backButton">
                <button> </button>
              </div>
              <div
                className="pw-backButton"
                onClick={() => {
                  setPasswordModalOpen(false);
                  setModalOpen(true);
                }}
              >
                <button></button>
              </div>
            </form>
          </div>
        </div>
      )}
    </>
  );
};

export default Header;
