import React from "react";
import PwChange from "../assets/images/PwChange.png";
import "./pwChange.css";
import "./myPage.css";
import { Link } from "react-router-dom";
const pswChange = () => {
  return (
    <div className="MyPage">
      <div className="pwChange-left">
        <Link to="/MyPage" style={{ textDecoration: "none" }}>
          <div className="MyPageIcon"> </div>
        </Link>

        <div className="MyPage-menu">
          <Link to="/myPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/QaPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/pswChange">
            <div className="menu"> </div>
          </Link>
          <Link to="/myPage/quit">
            <div className="menu"></div>
          </Link>
        </div>
      </div>

      <div className="pwChange-middle">
        <h3 className="pw-change">  </h3>
        <img
          className="pwChangeImage"
          src={PwChange}
          alt="  "
        />
      </div>
      <div className="pwChange-right">
        <div className="text-box">
          <div className="text"> </div>
          <div className="input">
            <input type="password" />
          </div>
        </div>
        <div className="text-box">
          <div className="text"> </div>
          <div className="input">
            <input type="password" />
          </div>
        </div>
        <div className="text-box">
          <div className="text"> </div>
          <div className="input">
            <input type="password" />
          </div>
        </div>
        <div className="change-button">
          <button className="button-style"></button>
        </div>
      </div>
    </div>
  );
};

export default pswChange;
