import React from "react";
import "./myPage.css";
import "./QaPage.css";
import { Link } from "react-router-dom";
const QaPage = () => {
  const QnAData = [
    {
      num: 6,
      classification: " ",
      name: "  .",
      day: "24.07.04",
    },
    {
      num: 5,
      classification: " ",
      name: "00   .",
      day: "24.07.04",
    },
    {
      num: 4,
      classification: " ",
      name: "00   .",
      day: "24.07.03",
    },
    {
      num: 3,
      classification: " ",
      name: "  .",
      day: "24.07.02",
    },
    {
      num: 2,
      classification: " ",
      name: "   .",
      day: "24.07.01",
    },
    {
      num: 1,
      classification: " ",
      name: "    .",
      day: "24.06.25",
    },
  ];
  return (
    <div className="MyPage">
      <div className="QaPage-left">
        <Link to="/MyPage" style={{ textDecoration: "none" }}>
          <div className="MyPageIcon"> </div>
        </Link>
        <div className="MyPage-menu">
          <Link to="/myPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/QaPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/pswChange">
            <div className="menu"> </div>
          </Link>
          <Link to="/myPage/quit">
            <div className="menu"></div>
          </Link>
        </div>
      </div>
      <div className="QaPage-right">
        <div className="QaIcon"> </div>

        <table className="QnA-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {QnAData.map((QnA, index) => (
              <tr key={index} className="tr-border">
                <td>{QnA.num}</td>
                <td>{QnA.classification}</td>
                <td>{QnA.name}</td>
                <td>{QnA.day}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default QaPage;
