const Seat = () => {
  const [selectedSeat, setSelectedSeat] = useState(""); //  "" 

  const handleSelectChange = (event) => {
    setSelectedSeat(event.target.value); //    
  };

  return (
    <div className="tk">
      <p className="kk">  VS   </p>
      <img src={JBS} alt="" className="tkI" />
      <div>
        <div className="Seat-choose">
          <select value={selectedSeat} onChange={handleSelectChange}>
            <option value=""> </option> {/*  ""  */}
            <option value="club1">A</option>
            <option value="club2">B</option>
            <option value="club3">C</option>
            <option value="club4">D</option>
            <option value="club5">E</option>
            <option value="club6">F</option>
            <option value="club7">G</option>
            <option value="club8">H</option>
            <option value="club9">I</option>
            <option value="club10">J</option>
            <option value="club11">K</option>
          </select>
        </div>

        <div className="Buy">
          <button className="tk-button"></button>
        </div>
      </div>{" "}
    </div>
  );
};

export default Seat;
