import React, { useState } from "react";
import "./gameList.css";
import DateSelector from "./DateSelector";
import Game from "../components/Game/Game";

// import matchList from "./matchList";

const GameList = () => {
  const [selectedClub, setSelectedClub] = useState("");

  const handleClubChange = (event) => {
    setSelectedClub(event.target.value);
  };

  return (
    <div className="league">
      <div className="leaguered">
        <h1 className="league_ticket">K LEAGUE /</h1>
      </div>

      <div className="select-box">
        <select value={selectedClub} onChange={handleClubChange}>
          <option value=""> </option>
          <option value="club1"> </option>
          <option value="club2"></option>
          <option value="club3"></option>
          <option value="club4"></option>
          <option value="club5">FC</option>
          <option value="club6">FC </option>
          <option value="club7"></option>
          <option value="club8"></option>
          <option value="club9"></option>
          <option value="club10"> FC</option>
          <option value="club11">  FC</option>
          <option value="club12"> </option>
        </select>
      </div>

      <div className="Date">
        <DateSelector />
      </div>

      <Game />
    </div>
  );
};

export default GameList;
