import React from "react";
import "./myPage.css";
import informationButton from "../assets/images/informationButton.png";
import { Link } from "react-router-dom";
const myPage = () => {
  return (
    <div className="MyPage">
      <div className="MyPage-left">
        <Link to="/MyPage" style={{ textDecoration: "none" }}>
          <div className="MyPageIcon"> </div>
        </Link>

        <div className="MyPage-menu">
          <Link to="/myPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/QaPage">
            <div className="menu"></div>
          </Link>
          <Link to="/myPage/pswChange">
            <div className="menu"> </div>
          </Link>
          <Link to="/myPage/quit">
            <div className="menu"></div>
          </Link>
        </div>
      </div>
      <div className="MyPage-right">
        <div>
          <h3 className="ticket-list"></h3>
          <div className="ticket-explanation">
                  .
          </div>
          <div className="ticket-calender">
            <input type="date" />
            <input type="date" />
          </div>
          <div className="information">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
          <div className="information2">
            <p>  .</p>
          </div>

          <div className="information-button">
            <img
              className="information-button"
              src={informationButton}
              alt=" "
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default myPage;
